$card-color: #99d8d0;
$card-text: #170a19;

.movies-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-gap: 20px;

  .form-control {
    grid-column: 1/-1;
    border: 0;
    color: $card-text;
    background-color: $card-color;
    opacity: 0.8;
  }
  .btn-toolbar {
    grid-column: 1/-1;
  }
}
